<template>
  <div class="page page-with-padding buttons">
    <wv-button type="primary" @click="showDialog('ios', '')">iOS Dialog </wv-button>
    <wv-button type="primary" @click="showDialog('ios')">iOS Dialog </wv-button>
    <wv-button type="primary" @click="showDialog('android', '')">Android Dialog </wv-button>
    <wv-button type="primary" @click="showDialog('android')">Android Dialog </wv-button>
  </div>
</template>

<script>
  import Dialog from '../../src/components/dialog'

  export default {
    methods: {
      doSomething () {
        console.log('hello')
      },

      showDialog (skin, title) {
        Dialog({
          title: title,
          message: 'hello world',
          skin
        },
        this.doSomething)
      }
    }
  }
</script>

<style scoped lang="scss">
  .buttons {
    display: block;
    width: 80%;
    margin: 20px auto;
  }
</style>



